const axios = require("axios");
import DynamicJS from "@/js/core/dynamicjs";
const serverURL = "https://applet2.500apps.com/${app}";
const unifiedURL = "https://appletio-js.500apps.com";
const uploadFileURL = "https://api.${window.region}.500apps.com/fs/upload/${app}?content_type=application/${type}&path=${folderId}";
const apiKey = require("./apiKeys.json");

class Upload {
  /**
   * Load Unified JS in Ninjacapture
   */
  async initJS() {
    let url = window.region == "dev" || window.region == "qa" ? `${unifiedURL}/${window.region}/main.js` : `${unifiedURL}/main.js`;
    let res = await DynamicJS.loadWithPromise(url);
    if (res == "done") window._AppletIO.init(apiKey[window.region]);
  }
  /**
   * Initiate upload file
   * @param {String} app - App Name
   * @param {String} s3_url - S3 URL
   * @param {String} fileName - File Name
   */
  async initUpload(app, s3_url, fileName) {
    this.url = eval("`" + serverURL + "`");
    // Get authentication ID
    await this.authenticate(app);
    // Get folder ID
    let folderId = app == "Dropbox" ? "NinjaCapture" : await this.getFolderId(app);
    // Upload File
    let response = await this.uploadFile(app, folderId, s3_url, fileName);

    // Get file name
    if (app == "Box") fileName = response.data.name;

    // eslint-disable-next-line no-prototype-builtins
    let url = !response.hasOwnProperty("error") ? await this.getFile(folderId, fileName, app) : response;
    // Get File
    return url;
  }

  /**
   * Authenticate Cloud App
   * @param {String} app - App Name
   */
  async authenticate(app) {
    // authenticate app
    let response = await window._AppletIO.getOAuth().authenticate(app);
    this.headers = {};
    this.token = JSON.parse(response.data.token.replace(/'/g, '"')).access_token;
    ["client-id", "client-secret", "refresh-token", "token-url"].forEach((key) => (this.headers[key] = response.data[key.replace("-", "_")]));
    this.headers.Authorization = `Bearer ${apiKey[window.region]}`;
  }

  /**
   * Get Folder ID
   * @param {String} app - App Name
   */
  async getFolderId(app) {
    if (app === "Box") {
      this.headers["access-token"] = this.token;
    }
    // Get Folders
    let folders = await axios.get(`${this.url}/folders`, { headers: this.headers });
    // Get Folder
    let folder = folders.data.find((i) => i.name == "NinjaCapture");
    if (folder && folder.id) return folder.id;
    // Create Folder if doesn't exist
    let folderId = await this.createFolder(app);
    return folderId;
  }

  /**
   * Create Folder
   */
  async createFolder() {
    let form = { folder_name: "NinjaCapture" };
    // Create Folder
    let folderData = await axios.post(`${this.url}/create-folder`, form, { headers: this.headers });
    let folderId = folderData.data.response ? JSON.parse(folderData.data.response).id : folderData.data.id;
    return folderId;
  }

  /**
   * Upload file to cloud App
   * @param {String} app - App Name
   * @param {String} userEmail - User Email
   * @param {String} s3_url - S3 URL
   * @param {String} fileName - File Name
   */
  async uploadFile(app, folderId, s3URL, fileName) {
    const headers = { "Access-Control-Allow-Origin": "*" };
    // Convert to blob
    let blob = await fetch(s3URL, { headers }).then((r) => r.blob());
    // eslint-disable-next-line
    let type = app == "Google Drive" ? "json" : "octet-stream";
    app = app == "Google Drive" ? "gdrive" : app.toLowerCase();
    const formData = new FormData();
    formData.append("file", blob, fileName);
    let url = eval("`" + uploadFileURL + "`");
    // Upload to cloud
    const response = await axios
      .post(url, formData, {
        headers: {
          token: this.token,
        },
      })
      .catch((error) => {
        if (app === "box" && error.response.data.error && error.response.data.error === "Error item_name_in_use") {
          return { error: "This file already exists in Box App. Save it again with the new title." };
        } else if (error.response.data.error && error.response.data.error === "Error Invalid Credentials") {
          return { error: `For ${app}, Credentials have expired. Click edit icon to reauthenticate the app and save again.` };
        }
      });
    return response;
  }

  /**
   * Get File Url
   * @param {String} folderId - Folder ID
   * @param {String} fileName - File Name
   * @param {String} app - App Name
   */
  async getFile(folderId, fileName, app) {
    if (app == "Dropbox") {
      fileName = encodeURIComponent(fileName).replace(/%20/g, "+");
      return `https://www.dropbox.com/home/NinjaCapture?preview=${fileName}`;
    }
    // Assign folder Id to params
    this.headers["folder-id"] = folderId;

    if (app === "Box") {
      // this.url = eval("`" + boxURL + "`");
      this.headers["access-token"] = this.token;
    }
    // get files
    let files = await axios.get(`${this.url}/filesinfolder`, { headers: this.headers });
    // Get File Id
    let id = files.data.find((i) => i.name == fileName).id;
    let url = app == "Box" ? `https://app.box.com/file/${id}` : `https://drive.google.com/file/d/${id}/view?usp=sharing`;
    return url;
  }
}
export default new Upload();
