const axios = require("axios");
import Data from "../core/data";
import { decompressFromEncodedURIComponent as lzStringDecompress } from "lz-string";
const Ninjacapture_Server = "https://api.${region}.500apps.com/ninjacapture/${window.domain}/${window.user}";
const Comments_Server = "https://api.${region}.500apps.com/ninjacapture/comments/${window.domain}/${window.user}";
const Password_Server = "https://api.${region}.500apps.com/ninjacapture/validate/${window.domain}/${window.user}";
var Shortcut_Server = "https://api.500apps.com/shortcuts/shortcut";
const File_Manager_URL = "https://api.${region}.500apps.com/files";
const Pcors = "https://my.${region}.500apps.com/pcors";

class NinjaCapture extends Data {
  constructor() {
    super(Ninjacapture_Server);
  }

  async init() {
    // Check compatibility (Here 8 is nothing but shortcut url length)
    var shortcutURL = location.pathname.split("/")[1].length === 8;
    // Get path from the shortcut url or LZString url
    if (shortcutURL) {
      var baseURL = await axios.get(`https://api.500apps.com/shortcuts/shortcut${location.pathname}`);
      var url = baseURL.data[0].link;
    } else url = location.pathname;
    // Decompress ninjacapture token ,which is from location path
    return window.location.href.includes("/nc") ? lzStringDecompress(url.split("/")[shortcutURL ? 4 : 2]) : lzStringDecompress(url.split("/")[shortcutURL ? 3 : 1]);
  }

  // Make axios request for third party API
  getExternalURL(url, blob) {
    let server_url = `${Pcors}?url=${url}`;
    this.url = eval("`" + server_url + "`");
    return axios.get(this.url, { responseType: blob ? "blob" : "json" });
  }

  async get(id) {
    let server_url = `${Ninjacapture_Server}/${id}`;
    this.url = eval("`" + server_url + "`");
    return await super.get();
  }

  async getComments(id) {
    let server_url = `${Comments_Server}/${id}`;
    this.url = eval("`" + server_url + "`");
    return await super.get();
  }

  async addComment(form, id) {
    let server_url = `${Comments_Server}/${id}`;
    this.url = eval("`" + server_url + "`");
    let apps = await axios.post(this.url, form);
    return apps.data;
  }

  async checkPassword(id, password) {
    let server_url = `${Password_Server}/${id}`;
    this.url = eval("`" + server_url + "`");
    let response = await axios.post(this.url, { password });
    return response.data;
  }

  /**
   * Get base URL based on shortcut
   * @param {String} shortcutLink Shortcut link from location path
   */
  async getBaseURL(shortcutLink, region) {
    if (region) Shortcut_Server = `https://api.${region}.500apps.com/shortcuts/shortcut`;
    let server_url = `${Shortcut_Server}${shortcutLink}`;
    this.url = eval("`" + server_url + "`");
    return await super.get();
  }

  // eslint-disable-next-line no-unused-vars
  async upload(file, fileName) {
    let app_name = "ninjacapture";
    let upload_response = await this.getSignedLink(`${app_name}`, "post-object", `${fileName}.webm`, `video/webm`, "public-read", window.domain);
    // Converting file object into formData
    var formData = new FormData();
    Object.keys(upload_response.presign_url.fields).forEach((field) => {
      formData.append(field, upload_response.presign_url.fields[field]);
    });
    formData.append("file", file);
    this.url = upload_response.presign_url.url;
    // Don't need Authorization token here
    await super.add(formData, true);
    return upload_response.presign_url;
  }

  async getSignedLink(appname, postType, file, contentType, acl, id) {
    // eslint-disable-next-line
    let server_url = `${File_Manager_URL}/public/signed/${id}?app=${appname}&key=${escape(file)}&type=${postType}&content_type=${contentType}&acl=${acl}`;
    this.url = eval("`" + server_url + "`");
    this.disablePCors();
    let results = await super.get();
    return results;
  }
}

export default new NinjaCapture();
