<template>
  <div>
    <div>
      <!-- Navbar -->
      <b-navbar variant="white" class="border-bottom px-3 py-2 mobile-nav-fixed">
        <b-container fluid>
          <div class="navbar-brand pb-0 ml-3">
            <!-- Logo -->
            <a class="router-link-active" href="https://ninjacapture.com/">
              <div>
                <img src="../../assets/ninjacapture/logo.png" alt="Ninjacapture" class="ninja-capture-logo" />
              </div>
            </a>
          </div>
          <div class="ml-auto mr-3 pr-md-2">
            <a href="https://500apps.com/" target="_blank" class="btn btn-primary btn-sm">Get NinjaCapture for Free</a>
          </div>
        </b-container>
      </b-navbar>
      <b-container fluid class="px-lg-3 image-details-page">
        <div class="justify-content-center m-0 pt-5 pt-md-3 row">
          <div class="lhs-width bg-white border-bottom border-left border-right border-top overflow-auto p-0  position-relative rounded h-100">
            <div class="custom-padding-nc-header border-bottom">
              <div class="d-flex justify-content-between">
                <div>
                  <h4 class="mb-0 my-auto text-left">{{ fileData.name }}</h4>
                  <!-- Description -->
                  <p class="font-size-sm mb-2 pb-1 text-muted text-left">{{ fileData.description }}</p>
                </div>
                <div class="position-zoom-btns" v-if="fileType.length > 0 && fileType == 'image'">
                  <b-button-group class="mt-n2 mb-2">
                    <b-button variant="white" size="sm" class="rounded-left-0 border-right-0 copy-icon" :disabled="scale == 1.5" @click="zoomIn()"
                      ><span class="fe fe-zoom-in align-middle font-size-lg text-gray-600"></span>
                    </b-button>
                    <b-button variant="white" size="sm" class="rounded-left-0 border copy-icon" :disabled="scale == 1" @click="zoomOut()"
                      ><span class="fe fe-zoom-out text-gray-600 align-middle font-size-lg"></span
                    ></b-button>
                  </b-button-group>
                </div>
              </div>
            </div>
            <!-- Image -->
            <div v-if="fileType.length > 0 && fileType == 'image'" class="img-wrapper" :style="`transform: scale(${scale}` + ')'" ref="draggable_container">
              <div class="custom-padding-nc-img" @mousedown="start_drag($event)" @mousemove="drag_image($event)" @mouseup="stop_drag()">
                <img class="border rounded  img-rounded img-fluid w-100" ref="img_ref" :src="fileData.url" />
              </div>
            </div>
            <!-- Video -->
            <div v-else-if="fileType.length > 0 && fileType == 'video'" class="pb-3 px-4 nc-video">
              <video ref="videoPlayer" class="border rounded img-rounded img-fluid col-12 px-0" :src="videoURL" controls autoplay></video>
            </div>
            <!-- Audio -->
            <audio v-else-if="fileType.length > 0 && fileType == 'audio'" class="mt-md-6 my-4 d-flex mx-auto  rounded px-4" :src="fileData.url" controls autoplay></audio>
            <iframe v-else class="border-0 h-100 mx-3 px-4" :src="fileData.url"></iframe>
          </div>

          <!-- Cloud Apps -->
          <div class="rhs-width mt-md-4 mt-lg-0">
            <b-card class="mb-3 cloud-scroll" body-class="custom-padding-cloud">
              <div class="d-flex justify-content-between align-items-center">
                <h4 class="mb-0">Save</h4>
              </div>
              <hr class="hr-margin" />
              <b-row class="px-1">
                <b-col lg="12" class="px-2" v-for="(app, index) in cloudApps" :key="index" ref="apps">
                  <b-card class="mb-0 pb-1 bg-transparent shadow-none border-0" body-class="p-0">
                    <div
                      class="text-center d-flex align-items-center border-bottom mb-2 pb-2"
                      v-if="index < cloudApps.length - 1"
                      @click="saveToCloud(app)"
                      :class="isCloudUploading[app] ? '' : 'cursor'"
                    >
                      <img :src="getLogo(app)" :alt="app" class="avatar-xs mr-2 rounded-circle" />
                      <p class="app-hover">{{ app }}</p>
                      <b-spinner v-if="isCloudUploading[app]" class="mt-1 cloud-spiner ml-2" variant="primary" label="Loading..." small></b-spinner>
                    </div>
                    <div class="text-center d-flex align-items-center" v-else @click="saveToCloud(app)" :class="isCloudUploading[app] ? '' : 'cursor'">
                      <img :src="getLogo(app)" :alt="app" class="avatar-xs mr-2 rounded-circle" />
                      <p class="app-hover">{{ app }}</p>
                      <b-spinner v-if="isCloudUploading[app]" class="mt-1 cloud-spiner ml-2" variant="primary" label="Loading..." small></b-spinner>
                    </div>
                  </b-card>
                  <p class="text-danger mb-0" v-if="Object.keys(cloudErrorMessage)">{{ cloudErrorMessage.error }}</p>
                </b-col>

                <b-col lg="12" class="px-2" v-for="app in cloudApps" :key="app">
                  <!-- Cloud App URL -->
                  <b-form-group v-if="cloudURL[app]" class="mb-0 mt-1">
                    <label class="mb-1">{{ app }}</label>
                    <b-input-group class="mb-0 mr-sm-2 mb-sm-0 border rounded col px-0">
                      <b-form-input aria-describedby="input-live-help input-live-feedback" class="border-left-0 border-top-0 border-bottom-0" :value="cloudURL[app]" trim disabled></b-form-input>
                      <b-button variant="bg-light" class="rounded-left-0 copy-icon" @click="copyLink(cloudURL[app], app)">
                        <span v-if="isCopied[app]" class="fe fe-check-circle text-success" v-b-tooltip.hover title="Copied"></span>
                        <span v-else class="fe fe-clipboard text-primary" v-b-tooltip.hover title="Copy"></span>
                      </b-button>
                    </b-input-group>
                  </b-form-group>
                </b-col>
                <!-- Alert message -->
                <b-col lg="12">
                  <b-alert v-model="toaster" class="showToster alert alert-warning mb-0 cloud-msg mt-2 rounded p-2" variant="warning" dismissible
                    ><span class="text-dark align-bottom">Save is in progress, please wait until it completes.</span>
                  </b-alert>
                </b-col>
              </b-row>
            </b-card>
            <b-card class="mb-3" body-class="p-3">
              <!-- we will do later for download -->
              <div class="d-block">
                <a class="d-flex justify-content-start align-items-center cursor-pointer" @click="download()">
                  <div class="d-flex align-items-center">
                    <b-img src="/img/download-icon.png" width="24" />
                    <div>
                      <span class="text-body hover-color ml-2">
                        Download
                      </span>
                    </div>
                  </div>
                </a>
              </div>
            </b-card>
            <b-card class="comment-height mobile-height-comment" body-class="custom-padding-cloud">
              <h4 class="mb-1">Comments</h4>
              <hr class="hr-margin" />
              <!-- Title, Description & Comments -->
              <div class="comment-container h-100">
                <div class="h-100 overflow-auto">
                  <div class="col-md-12">
                    <div v-if="comments.length > 0">
                      <div class="comment-box mt-2">
                        <div class="d-flex mb-3" v-for="comment in comments" :key="comment.id">
                          <!-- Profile Image -->
                          <div class="mr-2">
                            <div class="vue-avatar--wrapper profile-pic text-capitalize">
                              <small>{{ comment.name[0] }}</small>
                            </div>
                          </div>
                          <div class="w-100">
                            <div class="ml-1 mt-n1">
                              <!-- Profile Name -->
                              <div class="d-flex justify-content-between">
                                <b class="font-size-sm">{{ comment.name }}</b>
                                <span class="text-muted ml-2" v-if="comment.comment_at.length > 0">at {{ comment.comment_at }}</span>
                                <!-- Profile Created Date -->
                                <p class="text-muted mb-0 small-2">{{ comment.created_date.substring(6, 16) }}</p>
                              </div>
                              <!-- Comment -->
                              <p class="font-size-sm mb-0 pt-1 text-body word-break">{{ comment.comment }}</p>
                            </div>
                          </div>
                        </div>
                      </div>
                    </div>
                    <div v-else class="align-items-center d-flex h-auto justify-content-center">
                      <div class="mb-0 pb-3 pt-3 text-center empty-state-img">
                        <b-img src="https://infinity.500apps.com/img/empty-states/projectsly/no-discussions.svg" class="img-fluid mb-3" />
                        <h4 class="mb-3">No Comments Yet</h4>
                      </div>
                    </div>
                  </div>
                </div>
                <div class="row mobile-comment">
                  <div class="bottom-fixed">
                    <!-- Name -->
                    <div class="border-top col-12 pt-3 px-1">
                      <input placeholder="Your name" class="form-control form-control-sm small" v-model="name" />
                      <small class="text-danger" v-if="isError.name && this.name.length == 0">Name is required</small>
                    </div>
                    <!-- Comment -->
                    <div class="col-12 mt-2 px-1">
                      <textarea placeholder="Leave a comment" class="form-control small" rows="1" v-model="comment"></textarea>
                      <small class="text-danger" v-if="isError.comment && this.comment.length == 0">Comment is required</small>
                    </div>
                    <!-- Buttons -->
                    <div class="col-12 mt-2 pt-1 px-1 text-right">
                      <button class="btn btn-secondary btn-sm mr-2" @click="hideComment()">Cancel</button>
                      <button class="btn btn-primary btn-sm" @click="addComment()">Comment</button>
                    </div>
                  </div>
                </div>
              </div>
            </b-card>
          </div>
        </div>
      </b-container>
      <b-modal
        id="waiting-modal"
        modal-class="waiting-modal"
        size="md"
        body-class="px-md-6 py-md-5"
        dialog-class="waiting-width"
        hide-footer
        hide-header
        hide-header-close
        no-close-on-backdrop
        no-fade
        centered
      >
        <div class="mobile-overlay-width">
          <div class="text-center responsive-margin py-1">
            <img src="/img/loading.gif" class="mb-3 mt-n2" width="50" height="50" />
            <h3 class="mb-1 text-body">Please wait...</h3>
            <p class="mb-3 text-body">We are processing your media file.</p>
            <b-progress class="loader">
              <b-progress-bar variant="primary" :value="100" animated></b-progress-bar>
            </b-progress>
          </div>
        </div>
      </b-modal>
      <b-modal ref="modal" hide-header hide-footer no-close-on-esc no-close-on-backdrop centered size="sm">
        <div class="d-flex" v-if="fileData.share_url_type == 'private'">
          <div class="my-4 mx-auto">
            <p class="mb-0 font-weight-bold">This {{ fileType }} is set to private. Ask the author to change the settings</p>
          </div>
        </div>
        <div class="my-4" v-else>
          <h3>
            <b>This {{ fileType }} is password protected</b>
          </h3>
          <label>Enter Password(sent in mail)*:</label>
          <input type="password" class="form-control border-right-0 border-left-0 border-top-0 rounded-0" v-model="password" />
          <p class="text-danger mb-0" v-if="errorMessage.length > 0">{{ errorMessage }}</p>
          <b-button variant="primary" size="sm" class="float-right px-4 my-3" @click="checkPassword()">Enter</b-button>
        </div>
      </b-modal>
    </div>
  </div>
</template>
<script>
  import NinjaCapture from "@/js/ninjacapture/ninjacapture";
  import copyToClipboard from "@/js/core/copy-to-clipboard";
  import Upload from "@/js/ninjacapture/upload";
  import { decompressFromEncodedURIComponent as lzStringDecompress } from "lz-string";
  import Client from "@/js/core/client";
  export default {
    data() {
      return {
        fileData: {},
        isCopied: {},
        isCloudUploading: {},
        cloudURL: {},
        cloudApps: ["Google Drive", "Dropbox", "Box"],
        extension: {
          image: "png",
          video: "webm",
          audio: "mp3",
        },
        showComment: false,
        comment: "",
        name: "",
        comments: [],
        isError: {
          name: false,
          comment: false,
        },
        userInfo: null,
        password: "",
        errorMessage: "",
        isDataLoaded: false,
        id: null,
        fileType: "",
        toaster: false,
        cloudErrorMessage: "",
        scale: 1,
        isDrag: false,
        isZoom: false,
        positions: {
          clientX: 0,
          clientY: 0,
        },
        overlayPosition: window.screen.width <= 768 ? "bottom:300px;" : "bottom:0;",
        browser_type: Client.getVisitorInfo().browser,
        videoURL: "",
      };
    },
    // Using vue-meta plugin to add meta tags to page
    metaInfo() {
      return {
        meta: [
          {
            property: "og:title",
            content: this.fileData.name,
          },
          {
            property: `og:${this.fileType}`,
            content: this.fileData.url,
          },
          {
            property: "og:description",
            content: this.fileData.description || "",
          },
        ],
      };
    },
    async mounted() {
      this.$root.$emit("bv::toggle::modal", "waiting-modal");
      await this.decodeURI();
      console.log(this.userInfo, this.fileType, this.id);
      Upload.initJS();
      this.getFileData();
      this.getComments();
    },
    watch: {
      scale: {
        handler() {
          if (this.scale === 1) {
            this.isZoom = false;
            // Reset the image to original position
            this.$refs.draggable_container.style.top = "";
            this.$refs.draggable_container.style.left = "";
          }
        },
      },
    },
    methods: {
      // Decode the URL to get user details
      async decodeURI() {
        // Check compatibility (Here 8 is nothing but shortcut url length)
        var shortcutURL = location.pathname.split("/")[1].length === 8;
        // Get path from the shortcut url or LZString url
        if (shortcutURL) {
          var baseURL = await NinjaCapture.getBaseURL(location.pathname, location.search.split("=")[1]);
          this.domainID = baseURL[0].domain_id;
          var url = baseURL[0].link;
        } else url = location.pathname;
        // Decompress ninjacapture token ,which is from location path
        [this.userInfo, this.fileType, this.id] = window.location.href.includes("/nc")
          ? lzStringDecompress(url.split("/")[shortcutURL ? 4 : 2]).split("/")
          : lzStringDecompress(url.split("/")[shortcutURL ? 3 : 1]).split("/");
      },
      // Zoom In the image
      zoomIn() {
        this.isZoom = true;
        if (this.scale >= 1.5) return;
        this.scale = this.scale + 0.1;
      },
      // Zoom Out the image
      zoomOut() {
        if (this.scale == 1) return;
        this.scale = this.scale - 0.1;
      },
      // Download media file
      async download() {
        let fileType = "application/octet-stream";
        var blob = await this.dataURLtoBlob(this.fileData.url, fileType);
        let fileName = `${this.fileData.name}.${this.extension[this.fileType]}`;
        let url = window.URL.createObjectURL(blob);
        this.downloadURI(url, fileName);
        window.URL.revokeObjectURL(url);
      },

      /**
       * Convert image from data URL to Blob
       * @param {Sting} dataURL media file
       * @param {String} type Mimetype
       */
      async dataURLtoBlob(dataURL, type) {
        // If file type is Audio or Video
        if (type !== "image") {
          try {
            const blob = await fetch(dataURL).then((r) => r.blob());
            return blob;
          } catch (err) {
            console.log(err);
          }
        }
        // Separate the mime data
        var arr = dataURL.split(",");
        var mime = arr[0].match(/:(.*?);/)[1];

        // Convert from base64 to raw binary data
        var bstr = atob(arr[1]);
        var n = bstr.length;

        // Create the buffer and set the bytes of the buffer
        var u8arr = new Uint8Array(n);
        while (n--) {
          u8arr[n] = bstr.charCodeAt(n);
        }

        // Add the ArrayBuffer to a blob
        return new Blob([u8arr], { type: mime });
      },

      /**
       * Create download link
       * @param {String} URL // Respective media file
       * @param {String} name // filename
       */
      downloadURI(URL, name) {
        let link = document.createElement("a");
        link.download = name;
        link.href = URL;
        link.click();
      },

      /**
       * Get File response
       */
      getFileData() {
        NinjaCapture.get(this.id).then(async (response) => {
          this.fileData = response;

          /**
           * Get file data from cloud
           * @param {String} URL cloud URL
           */
          if (window.location.href.includes("/nc") && this.fileType == "image") {
            var binaryResponse = await NinjaCapture.getExternalURL(this.fileData.url);
            this.fileData.url = binaryResponse.data;
          }
          // Show Modal if type is private or password protected
          if (response.share_url_type === "public") {
            this.videoURL = await this.getVideoURL(this.fileData);
            if (this.videoURL) {
              this.$root.$emit("bv::toggle::modal", "waiting-modal");
              this.isDataLoaded = true;
            }
          } else this.$refs.modal.show();
          // Show Created Date in Date Month, year Format
          // Ex: 11 Jun, 2021
          this.fileData.created_date = this.fileData.created_date.substring(5, 11) + "," + this.fileData.created_date.substring(11, 16);
        });
      },

      /**
       * Copy URL
       */
      copyLink(url, type) {
        let response = copyToClipboard.copy(url);
        if (response) {
          if (!this.isCopied[type]) this.$set(this.isCopied, type, true);
          else this.isCopied[type] = true;
          setTimeout(() => {
            this.isCopied[type] = false;
          }, 3000);
        }
      },
      /**
       * Save file to Cloud
       */
      async saveToCloud(app_name, index) {
        this.cloudErrorMessage = "";
        // Enable toaster message while user trigger cloud app
        this.toaster = true;
        /**
         * Disable cloud apps
         * @param {Boolean} flag App is in inactive case
         * @param {Boolean} Index App index
         */
        this.disableCloudApps(true, index);
        this.$set(this.isCloudUploading, app_name, true);
        // Check popup window state
        this.checkPopupState(app_name);
        let fileName = `${this.fileData.name}.${this.extension[this.fileType]}`;
        let response = await Upload.initUpload(app_name, this.fileData.url, fileName);
        // eslint-disable-next-line no-prototype-builtins
        response.hasOwnProperty("error") ? (this.cloudErrorMessage = response) : (this.cloudURL[app_name] = response);
        // this.cloudURL[app_name] = await Upload.initUpload(app_name, this.fileData.url, fileName);
        await this.copyLink(this.cloudURL[app_name], app_name);
        this.isCloudUploading[app_name] = false;
        // Disable toaster message while user trigger cloud app
        this.toaster = false;

        /**
         * Enable cloud apps
         * @param {Boolean} flag App is in active case
         */
        this.disableCloudApps(false);
      },

      /**
       * Hide loader & toaster message
       * @param {String} app Active app
       */
      checkPopupState(app) {
        var timer = setInterval(() => {
          if (window.oauthPopup) {
            clearInterval(timer);
            // Hide Loader
            this.isCloudUploading[app] = false;
            this.toaster = false;
            /**
             * Enable cloud apps
             * @param {Boolean} flag App is in active case
             */
            this.disableCloudApps(false);
          }
        }, 1000);
      },

      /**
       * Disable event based on cloud active app
       * @param {Boolean} flag App active or not
       * @param {Number} index App index
       */
      disableCloudApps(flag, index) {
        this.cloudApps.forEach((item, id) => {
          if (flag) {
            if (id !== index) this.$refs.apps[id].style.pointerEvents = "none";
          } else this.$refs.apps[id].style.pointerEvents = "visible";
        });
      },
      /**
       * Get App Logo
       */
      getLogo(app) {
        app = app.toLowerCase().replace(" ", "-");
        return require(`@/assets/ninjacapture/${app}.svg`);
      },
      /**
       * Get Comments
       */
      getComments() {
        NinjaCapture.getComments(this.id).then((response) => {
          this.comments = response;
        });
      },
      /**
       * Add Comment
       */
      addComment() {
        if (this.comment.length === 0 || this.name.length === 0) {
          if (this.comment.length === 0) this.isError.comment = true;
          if (this.name.length === 0) this.isError.name = true;
          return;
        }
        let form = {
          name: this.name,
          comment: this.comment,
        };
        NinjaCapture.addComment(form, this.id).then(() => {
          this.getComments();
          this.hideComment();
        });
      },
      /**
       * Hide comment input
       */
      hideComment() {
        this.showComment = false;
        this.comment = "";
        this.name = "";
        this.isError = {
          name: false,
          comment: false,
        };
      },
      /**
       * Check enter password is valid or not
       */
      checkPassword() {
        if (this.password.length === 0) {
          this.errorMessage = "Password is required";
          return;
        }
        NinjaCapture.checkPassword(this.id, this.password)
          .then(() => {
            this.$root.$emit("bv::toggle::modal", "waiting-modal");
            this.isDataLoaded = true;
            this.$refs.modal.hide();
            this.password = "";
            this.errorMessage = "";
          })
          .catch(() => {
            this.errorMessage = "Your password did not match. Please try again.";
          });
      },
      // Mousedown event listener
      start_drag(event) {
        this.$refs.img_ref.draggable = false;
        this.positions.clientX = event.clientX;
        this.positions.clientY = event.clientY;
        this.isDrag = true;
      },
      // Mousemove event listener
      drag_image(event) {
        // Return if mouse is up
        if (!this.isDrag || !this.isZoom) return;
        // Change cursor icon when drag is on
        this.$refs.draggable_container.style.cursor = "move";
        // Get the movement coordinates
        var movementX = this.positions.clientX - event.clientX;
        var movementY = this.positions.clientY - event.clientY;
        // Change the image location after drag
        this.positions.clientX = event.clientX;
        this.positions.clientY = event.clientY;
        // set the element's new position:
        this.$refs.draggable_container.style.top = this.$refs.draggable_container.offsetTop - movementY + "px";
        this.$refs.draggable_container.style.left = this.$refs.draggable_container.offsetLeft - movementX + "px";
      },
      // Mouseup event listener
      stop_drag() {
        // Reset cursor
        this.$refs.draggable_container.style.cursor = "";
        this.isDrag = false;
      },
      async getVideoURL(fileData) {
        if (this.browser_type !== "Safari") return fileData.url;
        else {
          var response = await NinjaCapture.getExternalURL(fileData.url, true);
          var fileBlob = new Blob([response.data], { type: "video/webm;codecs=h264" });
          var file = new File([fileBlob], fileBlob.type);
          const fileObject = new FormData();
          fileObject.append("file", file, file.name.split("/"[1]));
          var upload_response = await NinjaCapture.upload(fileBlob, this.fileData.name);
          return `${upload_response.url}${escape(upload_response.fields.key)}`;
        }
      },
    },
  };
</script>
<style lang="css" scoped>
  @import url("/css/ninjacapture/ninjacapture.css?v=infinity_release_tag");
</style>
